.container {
  padding: 0 20px;
}
@media (min-width: 1200px) {
  .container {
    max-width: 944px;
    padding: 0;
    margin: 0 auto;
  }
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.button {
  padding: 0;
  margin: 0;
  background: none;
  border: none;
  cursor: pointer;
  text-align: center;
  margin: 0 auto;
  display: block;
  border: 3px solid #ffffff;
  padding: 15px 35px;
  border-radius: 29px;
  font-weight: bold;
  font-size: 18px;
  line-height: 18px;
}
.body {
  margin: 0;
  font-size: 18px;
  font-family: "Open Sans";
  display: flex;
  flex-direction: column;
  min-height: 100%;
  position: relative;
}
.body__main-header {
  position: absolute;
  z-index: 1;
}
.main-header {
  background-color: rgba(29, 38, 49, 0.75);
  padding: 21px 0;
  width: 100%;
}
@media (min-width: 660px) {
  .main-header {
    padding: 27px 0 43px 0;
  }
}
.main-header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 660px) {
  .main-header__container {
    align-items: baseline;
  }
}
.main-header__logo--tablet {
  display: none;
}
.main-header__logo--desktop {
  display: none;
}
@media (min-width: 660px) {
  .main-header__logo--mobile {
    display: none;
  }
  .main-header__logo--tablet {
    display: block;
  }
}
.navigation__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: none;
}
.navigation__item {
  width: 100%;
}
.navigation__link {
  display: block;
  color: #ffffff;
  text-decoration: none;
  text-transform: uppercase;
  font-weight: bold;
}
.burger {
  padding: 0;
  margin: 0;
  background: none;
  border: none;
  cursor: pointer;
}
.big-title {
  margin: 0;
  font-size: 24px;
  text-align: center;
  font-weight: 400;
}
@media (min-width: 660px) {
  .big-title {
    font-size: 40px;
    line-height: 30px;
  }
}
@media (min-width: 660px) {
  .banner {
    position: relative;
    background: url("../img/background-banner-tablet.jpg") no-repeat;
    min-height: 729px;
    padding: 210px 20px 30px 20px;
    box-sizing: border-box;
    background-size: cover;
    background-position: 100% center;
  }
  .banner::before {
    content: "";
    background: url("../img/phone-image-banner-tablet.png") no-repeat;
    position: absolute;
    left: 0;
    bottom: 0;
    display: block;
    width: 660px;
    height: 545px;
    background-size: 660px auto;
    left: 50%;
    transform: translateX(-50%);
  }
}
.banner__title {
  color: #ffffff;
}
@media (min-width: 660px) {
  .banner__title {
    line-height: 45px;
    width: calc(100% - 51.6%);
    margin-bottom: 39px;
  }
}
.banner__top {
  background: url("../img/background-banner-mobile.jpg") no-repeat;
  min-height: 462px;
  box-sizing: border-box;
  padding-top: 95px;
  background-size: cover;
  background-position: 100% center;
  position: relative;
}
@supports (background: url("../img/background-banner-mobile.webp")) {
  .banner__top {
    background: url("../img/background-banner-mobile.webp") no-repeat;
    background-size: cover;
  }
}
@media (min-resolution: 2dppx) {
  .banner__top {
    background: url("../img/background-banner-mobile@2x.jpg") no-repeat;
    background-size: cover;
  }
  @supports (background: url("../img/background-banner-mobile@2x.webp")) {
    .banner__top {
      background: url("../img/background-banner-mobile@2x.webp") no-repeat;
      background-size: cover;
    }
  }
}
@media (min-width: 660px) {
  .banner__top {
    background: none;
    min-height: auto;
    padding: 0;
  }
}
.banner__top::before {
  content: " ";
  background: url("../img/phone-image-banner-mobile.png") no-repeat;
  position: absolute;
  left: 0;
  bottom: 0;
  display: block;
  width: 320px;
  height: 286px;
  background-size: 320px auto;
  left: 50%;
  transform: translateX(-50%);
}
@supports (background: url("../img/phone-image-banner-mobile.webp")) {
  .banner__top::before {
    background: url("../img/phone-image-banner.webp") no-repeat;
    background-size: 320px auto;
  }
}
@media (min-resolution: 2dppx) {
  .banner__top::before {
    background: url("../img/phone-image-banner-mobile@2x.png") no-repeat;
    width: 320px;
    height: 286px;
    background-size: 320px auto;
  }
  @supports (background: url("../img/phone-image-banner-mobile@2x.webp")) {
    .banner__top::before {
      background: url("../img/phone-image-banner-mobile@2x.webp") no-repeat;
      background-size: 320px auto;
    }
  }
}
@media (min-width: 660px) {
  .banner__top::before {
    display: none;
  }
}
.banner__bottom {
  background-color: #1D2631;
  padding-top: 28px;
  padding-bottom: 38px;
}
@media (min-width: 660px) {
  .banner__bottom {
    padding: 0;
    background: none;
    width: calc(100% - 51.6%);
    position: relative;
    z-index: 20;
  }
}
.banner__button {
  color: #ffffff;
  text-decoration: none;
  margin-bottom: 32px;
}
@media (min-width: 660px) {
  .banner__button {
    margin-bottom: 46px;
  }
}
.banner__text {
  margin: 0;
  color: #ffffff;
  font-size: 14px;
  line-height: 24px;
  text-align: center;
}
@media (min-width: 660px) {
  .banner__text {
    font-size: 17px;
  }
}
.banner__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  margin-bottom: 28px;
}
@media (min-width: 660px) {
  .banner__list {
    margin-bottom: 38px;
  }
}
.banner__item {
  display: block;
}
.banner__item--apple::before {
  content: url("../img/icons/apple.svg");
  display: block;
  width: 47px;
  height: 56px;
}
.banner__item--android::before {
  content: url("../img/icons/android.svg");
  display: block;
  width: 43px;
  height: 47px;
}
.banner__item--microsoft::before {
  content: url("../img/icons/microsoft.svg");
  display: block;
  width: 44px;
  height: 41px;
}
.benefits {
  padding-top: 46px;
}
.benefits__title {
  margin-bottom: 40px;
}
.benefits__title:last-child {
  margin-bottom: 0;
}
.benefits__header {
  margin-bottom: 41px;
}
.benefits__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  position: relative;
  overflow: hidden;
  padding: 85px 20px 325px 20px;
  background-color: #f2f2f2;
}
.benefits__list::before {
  content: " ";
  height: 33px;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  -webkit-clip-path: polygon(0 100%, 0 0, 100% 0);
  clip-path: polygon(0 100%, 0 0, 100% 0);
  background-color: #ffffff;
}
.benefits__list::after {
  content: "";
  background: url("../img/phone-image-example.png") no-repeat;
  width: 157px;
  height: 320px;
  background-size: 157px 320px;
  display: block;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: -37px;
}
@supports (background: url("../img/phone-image-example.webp")) {
  .benefits__list::after {
    background: url("../img/phone-image-example.webp") no-repeat;
    background-size: 157px 320px;
  }
}
@media (min-resolution: 2dppx) {
  .benefits__list::after {
    background: url("../img/phone-image-example@2x.png") no-repeat;
    background-size: 157px 320px;
  }
  @supports (background: url("../img/phone-image-example@2x.webp")) {
    .benefits__list::after {
      background: url("../img/phone-image-example@2x.webp") no-repeat;
      background-size: 157px 320px;
    }
  }
}
.benefits__advantage {
  text-align: center;
  margin-bottom: 43px;
}
.benefits__advantage:last-child {
  margin-bottom: 0;
}
.benefits__advantage-title {
  font-size: 18px;
  line-height: 24px;
  margin: 0;
  margin-bottom: 15px;
}
.benefits__advantage-text {
  margin: 0;
  font-size: 14px;
  line-height: 24px;
}
.reviews {
  padding: 50px 0 31px 0;
}
.reviews__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  margin-bottom: 12px;
}
.reviews__user {
  font-size: 18px;
  line-height: 30px;
  text-align: center;
}
.reviews__text {
  margin: 0;
  font-size: 14px;
  line-height: 30px;
  text-align: center;
}
.switches-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  column-gap: 19px;
}
.switches-list__button {
  width: 6px;
  height: 6px;
  margin: 0;
  padding: 0;
  background-color: rgba(29, 38, 49, 0.3);
  border: none;
  border-radius: 50%;
  cursor: pointer;
}
.prices {
  background-color: #f2f2f2;
  padding: 83px 0 49px 0;
  position: relative;
  overflow: hidden;
}
.prices::before {
  content: " ";
  height: 20px;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  -webkit-clip-path: polygon(0 100%, 0 0, 100% 0);
  clip-path: polygon(0 100%, 0 0, 100% 0);
  background-color: #ffffff;
}
.prices__container {
  padding: 0;
}
.prices__table {
  width: 832px;
  position: relative;
  left: 50%;
  transform: translateX(-50%);
  margin-bottom: 37px;
}
.table {
  padding: 0;
  border-spacing: 3.1px;
}
.table-head__tariff {
  position: relative;
  overflow: hidden;
  padding: 19px 95px 35px 95px;
  background-color: #283645;
}
.table-head__tariff--hit {
  background: url("../img/icons/hit-ribbon.svg") no-repeat;
  background-color: #283645;
  background-size: 88px auto;
  background-position: -10px 0px;
}
.table-head__tariff--hit::before {
  content: attr(data-hit-text);
  display: block;
  position: absolute;
  top: 21px;
  left: 14px;
  transform: rotate(-45deg);
  font-size: 14px;
  font-weight: 400;
  color: #ffffff;
  text-transform: uppercase;
}
.table-head__tariff-title {
  display: block;
  margin-bottom: 10px;
  font-size: 16px;
  font-weight: bold;
  text-transform: uppercase;
  color: #ffffff;
}
.table-head__tariff-price {
  display: block;
  font-size: 18px;
  font-weight: 400;
  color: #ffffff;
}
.table-body__cell {
  padding: 19px 17px 19px 20px;
  background-color: #ffffff;
}
.table-body__filter-title {
  display: none;
}
.table-body__feature {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.table-body__feature--pink::before {
  content: attr(data-feature-name);
}
.table-body__feature--emoticons::before {
  content: attr(data-feature-name);
}
.table-body__feature--comments::before {
  content: attr(data-feature-name);
}
.table-body__availability--yes::before {
  content: url("../img/icons/yes.svg");
  display: block;
  width: 31px;
  height: 21px;
}
.table-body__availability--no::before {
  content: url("../img/icons/no.svg");
  display: block;
  width: 27px;
  height: 27px;
}
.contacts {
  padding-top: 48px;
}
.contacts__title {
  margin-bottom: 45px;
}
.contacts__map {
  margin-top: 41px;
}
.contacts-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  text-align: center;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
  row-gap: 33px;
}
.contacts-list__title {
  font-size: 18px;
  font-weight: bold;
  margin: 0;
  margin-bottom: 13px;
}
.contacts-list__link {
  font-size: 24px;
  color: #d22856;
  text-decoration: none;
  font-weight: 400;
  word-wrap: break-word;
}
.map {
  position: relative;
}
.map::before {
  content: " ";
  height: 33px;
  width: 100%;
  position: absolute;
  top: -1px;
  left: 0;
  -webkit-clip-path: polygon(0 100%, 50% 100%, 0 0);
  clip-path: polygon(100% 0, 50% 100%, 0 0);
  background-color: #ffffff;
  z-index: 100;
}
.map__image {
  display: block;
  width: 100%;
  max-height: 200px;
  object-fit: cover;
}
.map__iframe {
  border: 0;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}
.main-footer {
  padding: 40px 0;
}
.main-footer__container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));
  gap: 38px;
  justify-items: center;
}
.main-footer__social-list {
  width: 180px;
}
.main-footer__copyright {
  width: 130px;
}
.social-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.social-list__link {
  display: flex;
  position: relative;
  width: 54px;
  height: 54px;
  border: 3px solid #f2f2f2;
  box-sizing: border-box;
  border-radius: 50%;
}
.social-list__link--vk::before {
  content: url("../img/icons/vk.svg");
  display: block;
  width: 18px;
  height: 18px;
  margin: auto;
}
.social-list__link--telegram::before {
  content: url("../img/icons/telegram.svg");
  display: block;
  width: 18px;
  height: 18px;
  margin: auto;
}
.social-list__link--youtube::before {
  content: url("../img/icons/youtube.svg");
  display: block;
  width: 18px;
  height: 18px;
  margin: auto;
}
.copyright {
  display: flex;
  align-items: center;
  justify-content: space-between;
  text-decoration: none;
  color: #1D2631;
}
.copyright__text {
  font-size: 14px;
  line-height: 18px;
}
